.friends-wrap {
  box-sizing: border-box;
  width: 100%;
  height: auto;
  background-color: #0f0f0f;
}

.friends-list-wrap {
  box-sizing: border-box;
  width: 100%;
  height: auto;
  background-color: #0f0f0f;
  position: relative;
  margin-bottom: 60px;
}

.friends-list-wrap ul {
  position: relative;
}

.li-col {
  position: relative;
}

.li-col:last-child {
  border-bottom: 1px solid #1b1b1b;
}

.thumb-img-s-my {
  background: url('http://gdimg.gmarket.co.kr/982755416/still/600?ver=1553497509') no-repeat top center;
  background-size: 100%;
  width: 40px;
  height: 40px;
  display: inline-block;
  border-radius: 1em;
  margin: 16px;
  vertical-align: middle;
}

.text {
  color: white;
  display: inline-block;
  vertical-align: middle;
  font-size: 12px;
}

.text h4,
.text p {
  line-height: 170%;
  padding: 0;
  margin: 0;
}

.text p {
  font-size: 0.8em;
}


.thumb-img-s-normal {
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 0.75em;
  margin: 16px;
  vertical-align: middle;
}

.thumb-img-s-channel {
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 0.75em;
  margin: 16px;
  vertical-align: middle;
}

.thumb-img-s-birth {
  background: coral url('https://icons-for-free.com/iconfiles/png/512/gift+holidays+party+present+icon-1320086997440048466.png') no-repeat top center;
  background-size: 100%;
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 0.75em;
  margin: 16px;
  vertical-align: middle;
}

.thumb-img-s-recomm {
  background: darkviolet url('https://www.flaticon.com/svg/vstatic/svg/42/42767.svg?token=exp=1619500222~hmac=f4f3e3ef8a0b8b62b239155842698a75') no-repeat top 10px center;
  /* background-blend-mode: soft-light; */
  background-size: 40%;
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 0.75em;
  margin: 16px;
  vertical-align: middle;
}

.thumb-img-s-channel {
  background: yellow url('https://www.flaticon.com/svg/vstatic/svg/2360/2360201.svg?token=exp=1619501008~hmac=3ae03d7b00d03fc2afb7fd89b8d33c8c') no-repeat top 6px center;
  background-size: 60%;
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 0.75em;
  margin: 16px;
  vertical-align: middle;
}



h6 {
  margin: 0;
  padding: 10px 0 0 18px;
  color: white;
  font-weight: 300;
  opacity: 0.5;
}


.round-btn-s {
  border: 1px solid white;
  font-size: 0.6em;
  color: white;
  display: inline-block;
  padding: 4px 8px;
  border-radius: 100px;
  position: absolute;
  right: 16px;
  top: calc(50% - 10px);
}


.go-arrow-s {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 100px;
  position: absolute;
  right: 16px;
  top: calc(50% - 10px);
  font-size: 0.7em;
  color: #2b2b2b;
}

.gift-shop {
  border-color: red !important;
}

.music-play {
  border-color: green !important;
}


.red {
  color: red;
}

.green {
  color: green;
}

.update-img-dot {
  position: absolute;
  top: -4px;
  left: 14px;
  color: red;
  font-size: 1.5em;
}

.toggle-arrow-s {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 100px;
  position: absolute;
  right: 16px;
  top: 4px;
  font-size: 0.7em;
  color: #2b2b2b;
}